body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(#070020), to(#0B4751));
  background: -webkit-linear-gradient(top, #070020, #0B4751);
  background: -o-linear-gradient(top, #070020, #0B4751);
  background: linear-gradient(to bottom, #070020, #0B4751);
  height: 100vh;
}



